.main_home_container{
    width: 95%;
    margin: auto;
    min-height: 100vh;
    max-height: 100vh;
    display: flex;
    flex-direction: column;
    h1{
    color: var(--white2);
    }
}

.name-title{
    margin: 0px 0px 0px 8px;
    font-family: 'Lato';
    font-weight: 200;
    font-size: 4.7em;
    opacity: 0;
    letter-spacing: 8px;
    transform: translateY(-20vh);
    animation: title 1s ease-out 1 normal forwards, title-opacity 1s ease-in 1 normal forwards;
}

.sub-title{
    width: 0%;
    font-family: "Lato";
    font-weight: 200;
    font-size: 1.7em;
    margin: 0px 0px 0px 8px;
    color: var(--white2);
    overflow: hidden; /* Ensures the content is not revealed until the animation */
    border-right: 3px solid var(--white2); /* The typwriter cursor */
    white-space: nowrap; /* Keeps the content on a single line */
    animation: typing 2.5s steps(30, end) 0.5s forwards, blink-caret 1s step-end 2.8s infinite;
    white-space: nowrap;
}

.separator { 
    margin-top: 30px;
    margin-left: 10px;
    background: #EFEEEE;
    height: 1px;
    border: none;
    animation: fadeInSeparatorA 1.5s ease-in .5s 1 normal forwards;
}

.separator2 { 
    margin-top: 5px;
    margin-left: 10px; 
    margin-bottom: 50px;
    color: red; 
    background: #EFEEEE;
    height: 1px;
    border: none;
    animation: fadeInSeparatorB 1.5s ease-in .8s 1 normal forwards;
}

.home-button-container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    width: 100%;
    height: 245px;
    background: green;

    a{
        height:  55px;
        padding: 0;
        text-decoration: none;        
        transition: 0.4s;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        background: rgb(176,0,2);
        background: linear-gradient(90deg, rgba(176,0,2,1) 6%, rgba(180,23,23,0.7595413165266106) 89%);
        margin-right: 10px;
        margin-left: 10px;
        min-width: 180px;
        max-width: 180px;
        cursor: pointer;

        i{
         color: var(--white1);
         padding: 0;
         margin: 2.5px 5px 0px 5px;
         align-items: center;
        }
        
        h1 {
            transition: 0.4s;
            padding: 0;
            font-family: "Open Sans";
            font-size: 1em;
            color: #EFEEEE;
            margin: 2px;
            min-width: 130px;
            max-width: 130px;
            text-align: center;
        }      
    }
    a:hover {
        border: 2px var(--white1) solid ;
        background: transparent;
        border-radius: 10px;
        h1 {
            color: var(--white1);
            padding: 0;
        }
        i {
            color: var(--white1);
            padding: 0;
        }
    }
}

.arrow-container {
    margin: 0;
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: center;
    align-items: center;
    height: 130px;
    background: red;

    i{ 
        border-radius: 50%;
        padding: 5px;
        background: var(--white2);
        font-size: 40px;
        color: var(--black2);
        transition: 0.4s;
        opacity:0 ;
    }
    .arrow-animation {
        animation: arrowOpacity 1s ease-in 3s 1 normal forwards,
                   arrowMove 2s infinite ease-out 3s;

                   &:hover{
                    -webkit-animation-play-state:paused;
                    -moz-animation-play-state:paused;
                    -o-animation-play-state:paused;
                    animation-play-state:paused;
                    cursor: pointer;
                    font-size: 48px;
                  }
    }
}
.home-links-container {
width: 100%;
display: flex;
flex-direction: row;
justify-content: flex-end;
align-items: center;
height: 100px;

}

.iconA {
   opacity: 0;  
   animation: HomeIcons 1s ease 3s 1 normal forwards;
    img {
     transition: .4s;
    }
   &:hover {
     img {
         width: 43px;
         height: 43px;
     }
   }
}

.iconB {
    opacity: 0; 
    animation: HomeIcons 1s ease 2.5s 1 normal forwards;
    img {
        transition: .4s;
       }
      &:hover {
        img {
            width: 33px;
            height: 33px;
        }
      }
 }

@media screen and (max-width: 418px) {

    .home-button-container {
        justify-content: space-around;
        align-items: center;
        height: 170px;        
    }
}


@media screen and (max-width: 370px) and (orientation: landscape) {
    .home-button-container {
        background: red;
        height: 100px;
        justify-content: center;
    }
  }
 
