.main-contact-container {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}

.form-container {
    width: 50%;
    height: 100%;
    
}

.info-contact-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    margin: 0px 30px 0px 30px;
  
    height: 100%;

    h4{
        margin: 30px 0px 0px 0px;
        color: var(--white2);
        font-family: "Nunito";
    }
}

.phone-number {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;

    h4{
        margin: 0;
    }

    i {
        font-size: 18px;
        color: var(--white2);
        margin-right: 15px;
    }
}

.contacticons-container {

    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

        i{  
            margin: 10px;
            font-size: 50px;
            color: white;
    }

}

